import { Schema } from 'prosemirror-model'

// nodes 
let schema = {
  nodes: {
    doc: { content: 'block+' },
    paragraph: {
      group: 'block',
      content: 'text*',
      marks: '_',
      parseDOM: [{ tag: 'p' }],
      toDOM(node) {
        return ['p', 0]
      }
    },
    heading: {
      group: 'block',
      content: 'text*',
      marks: '',
      attrs: { level: { default: 1 } }
    },
    text: { inline: true }
  },
  marks: {
    strong: {},
    em: {}
  }
}

export function getSchema() {
  return new Schema(schema)
}
